import React, { useEffect, useState } from "react";
import axios from "axios";

export default function SubAuaStatsTable() {
  const [rows, setRows] = useState([]);

  const fetchData = () => {
    axios
      .get("http://localhost:8080/api/ssc-summary")
      .then((res) => setRows(res.data))
      .catch((err) => console.error("Error fetching summary:", err));
  };

  useEffect(() => {
    fetchData(); // initial load

    // Refresh every 2 minutes (120,000 ms)
    const interval = setInterval(fetchData, 120000);

    return () => clearInterval(interval); // cleanup on unmount
  }, []);

  return (
    <table border="1" cellPadding="8" style={{ borderCollapse: "collapse", width: "130%" }}>
      <thead>
        <tr>
          <th>Pused</th>
          <th colSpan={3}>Current</th>
          <th colSpan={3}>Recent (2 min)</th>
        </tr>
        <tr>
          <th></th>
          <th>Total</th>
          <th>Success (%)</th>
          <th>Failed (%)</th>
          <th>Total</th>
          <th>Success (%)</th>
          <th>Failed (%)</th>
        </tr>
      </thead>
      <tbody>
        {rows.map((r, i) => (
          <tr key={i}>
            <td>{r.pused}</td>
            <td>{r.currentTotal}</td>
            <td>
              {r.currentSuccess} ({r.currentSuccessPct.toFixed(1)}%)
            </td>
            <td>
              {r.currentFailed} ({r.currentFailedPct.toFixed(1)}%)
            </td>
            <td>{r.recentTotal}</td>
            <td>
              {r.recentSuccess} ({r.recentSuccessPct.toFixed(1)}%)
            </td>
            <td>
              {r.recentFailed} ({r.recentFailedPct.toFixed(1)}%)
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
