import React, { useEffect, useState } from "react";
import { Pie } from "react-chartjs-2";
import { Doughnut } from "react-chartjs-2";
import axios from "axios";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import SummaryCards from "./transactionsummary";
import SaBarChart from "./projectwise";
import SubAuaStatsTable from "./ssctransaction";



ChartJS.register(ArcElement, Tooltip, Legend);

const PUSED_COLORS = {
  "-OTP-": "#ff6385b2",
  "-TKN-": "#d9eb36ff",
  "AUTH_D": "#ffa033b2",
  "AUTH_F": "#4b7ec094",
  "AUTH_I": "#66ffab77",
  "AUTH_O": "#4b7ec056",
  "AUTH_P": "#5833ff5d",
  "AUTH_UNK": "#9c33ff8c",
  "KYC_F": "#339cffff",
  "KYC_I": "#33fff5ab",
  "KYC_OTP": "#ff333370",
  "KYC_P": "#ff3392ff",
  "KYC_UNK": "#339cffff"
};
const CARD_TO_PUSED = {
  TOTAL: ["-OTP-", "-TKN-", "AUTH_D", "AUTH_F", "AUTH_I", "AUTH_O", "AUTH_P", "KYC_F", "KYC_I", "KYC_P", "KYC_OTP"], 
  AUTH: ["AUTH_D", "AUTH_F", "AUTH_I", "AUTH_O", "AUTH_P"],
  KYC: ["KYC_F", "KYC_I", "KYC_P","KYC_OTP"],   
  OTP: ["-OTP-"],                                //  Only -OTP- here
  TOKEN: ["-TKN-"]
};
const getColorForPused = (pused) => PUSED_COLORS[pused] || "#999999";

export default function PusedDashboard() {
  const [chartData, setChartData] = useState(null);
  const [records, setRecords] = useState([]);
  const [activeCard, setActiveCard] = useState(null);
   const [filteredData, setFilteredData] = useState(null);

useEffect(() => {
  axios.get("http://10.254.9.46/ssc/api/montharchive")
    .then(res => {
      if (!res.data || res.data.length === 0) return;

      const group = {};
      res.data.forEach(r => {
      let key = r.pused || "UNKNOWN";
  
        const ret = r.ret === "y" ? "y" : "n";
        const value = r.transcation || 0;

        if (!group[key]) group[key] = { success: 0, failed: 0 };
        group[key][ret === "y" ? "success" : "failed"] += value;
      });

      const labels = Object.keys(group);
      const data = labels.map(p => group[p].success + group[p].failed);
      const backgroundColors = labels.map(p => getColorForPused(p));

      setChartData({
        labels,
        datasets: [
          {
            label: "Total Transactions",
            data,
            backgroundColor: backgroundColors
          }
        ]
      });

      // Prepare table data sorted by total descending
      let tableData = labels.map(p => ({
        pused: p,
        total: group[p].success + group[p].failed,
        success: group[p].success,
        failed: group[p].failed
      }));

      tableData.sort((a, b) => b.total - a.total);

      setRecords(tableData);
    })
    .catch(err => console.error("Error fetching data:", err));
}, []);
const handleCardClick = (cardName) => {
  if (!records || records.length === 0) return;

  if (activeCard === cardName) {
    // second click  close popup
    setActiveCard(null);
    setFilteredData(null);
    return;
  }

  // first click  filter data
  const relatedPused = CARD_TO_PUSED[cardName] || [];
  const filtered = records.filter((r) => relatedPused.includes(r.pused));

  setActiveCard(cardName);
  setFilteredData(filtered.length > 0 ? filtered : null);
};

  if (!chartData) return <p>Loading chart..Please Wait.</p>;

  // Customize tooltip to show total, success, failed
// Calculate total of all transactions from records
// Calculate total of all transactions from records
const totalAll = records.reduce((sum, r) => sum + r.total, 0);

// Pie chart options with percent tooltip for success and failed
const options = {
  plugins: {
    tooltip: {
      callbacks: {
        label: function(context) {
            
          const label = context.label || "";
          const record = records.find(r => r.pused === label);
          if (!record) return "";

          const percentOfAll = ((record.total / totalAll) * 100).toFixed(0);
          const successPercent = ((record.success / record.total) * 100).toFixed(0);
          const failedPercent = ((record.failed / record.total) * 100).toFixed(0);

          return [
            ` ${label}`,
            `Total: ${percentOfAll}%`,
            `Success: ${successPercent}%`,
            `Failed: ${failedPercent}%`
          ];
        
        },
          labelTextColor: function(context) {
          if (context.dataset.label === context.label) {
            return "#000"; // dark for first label
          }
          return "#fffefeff";   // slightly lighter for others
        }
      }
    }
  }
};


return (
  <div
    style={{
      minHeight: "100vh",
      backgroundColor:" #f7f7f7te",
      padding: "2px",
      display: "flex",
      flexDirection: "column", // column layout for top + bottom
      alignItems: "center"
    }}
  >
     <div style={{ 
        display: "flex", 
        justifyContent: "space-between",  // pushes logos to edges
        alignItems: "center",
        width: "100%"
      }}>
    <img src="http://10.254.9.46/ssc/NIC-LOGO-IDENTITY-VARIANTS_sans-01.png" alt="Left Logo" style={{ height: "100px" }} />
<img src="http://10.254.9.46/ssc/adhar (1).png" alt="Right Logo" style={{ height: "100px" }} />
    </div>
    
    <div style={{ 
    background: "#2f4f9d",   // blue background
  color: "white", 
  padding: "10px", 
  textAlign: "center", 
  fontSize: "22px", 
  fontWeight: "bold",
  width: "100%",  
            // full width
  position: "relative",     // stays in flow
  left: 0, 
  right: 0,
    top:0,
  bottom:0,

  
}}>
   <div style={{ fontSize: "22px", textAlign: "center", fontWeight: "bold" }}>
  Welcome To Aadhaar Authentications Dashboard
  </div>
</div>
    {/* Summary Cards Row */}
    <div   style={{
    width: "100%",       // full screen width
    padding: "0 20px",   // optional padding for spacing
    boxSizing: "border-box",
    marginTop:"10px",
     marginLeft:"-100px",

  }}>
     
    </div>
     <div style={{
    width: "50%",       // full screen width
    padding: "0 20px",   // optional padding for spacing
    boxSizing: "border-box",
    marginTop:"10px",
      marginLeft:"-100px",

  }}>
      {/* Example for "ePDS, Kerala" */}
      <SubAuaStatsTable subAuaId="Staff Selection Commission" />
    </div>
    {/* Charts + Table Row */}
  
  </div>
);}
