import React, { useEffect, useState } from "react";
import axios from "axios";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const SaBarChart = () => {
  const [chartData, setChartData] = useState(null);
  const [grouped, setGrouped] = useState({});

  useEffect(() => {
    axios
      .get("http://http://10.254.9.46/ssc/api/montharchiveprojectwise")
      .then((res) => {
        if (!res.data || res.data.length === 0) return;

        const groupedData = {};

        res.data.forEach((row) => {
          const sa = row.sa || row.sanull;
          const pused = row.pused || "UNKNOWN";
          const ret = row.ret === "y" ? "success" : "failed";
          const value = row.transcation || 0;

          if (!groupedData[sa]) {
            groupedData[sa] = {
              total: 0,
              success: 0,
              failed: 0,
              pused: {},
            };
          }

          groupedData[sa].total += value;
          groupedData[sa][ret] += value;

          if (!groupedData[sa].pused[pused]) {
            groupedData[sa].pused[pused] = { success: 0, failed: 0 };
          }
          groupedData[sa].pused[pused][ret] += value;
        });

              const entries = Object.entries(groupedData);

// sort by total transactions (descending)
entries.sort(([, a], [, b]) => b.total - a.total);

const labels = entries.map(([sa]) => sa);
const totals = entries.map(([, data]) => data.total);

setChartData({
  labels,
  datasets: [
    {
      label: "Total Transactions",
      data: totals,
      backgroundColor: "#2f879dff",
    },
  ],
});

        setGrouped(groupedData);
      })
      .catch((err) => console.error("Error fetching SA data:", err));
  }, []);

const options = {
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    tooltip: {
      backgroundColor: "#ffffff",
      titleColor: "#000",
      bodyColor: "#111",
      borderColor: "#444",
      borderWidth: 1,
      bodyFont: { family: "monospace", size: 13 },
      titleFont: { weight: "bold", size: 14 },
      padding: 10,
        position: "nearest",

      callbacks: {
        label: function (context) {
          const sa = context.label;
          const g = grouped[sa];
          if (!g) return "";

          // Collect all rows (ALL + each pused)
          const rows = [];

          // First push ALL row
          rows.push({
            pused: "ALL",
            total: g.total,
            success: g.success,
            successPct: g.total ? ((g.success / g.total) * 100).toFixed(0) : 0,
            failed: g.failed,
            failedPct: g.total ? ((g.failed / g.total) * 100).toFixed(0) : 0,
          });

          // Add breakdown rows
          Object.entries(g.pused).forEach(([p, vals]) => {
            const totalPused = vals.success + vals.failed;
            rows.push({
              pused: p,
              total: totalPused,
              success: vals.success,
              successPct: totalPused
                ? ((vals.success / totalPused) * 100).toFixed(0)
                : 0,
              failed: vals.failed,
              failedPct: totalPused
                ? ((vals.failed / totalPused) * 100).toFixed(0)
                : 0,
            });
          });

          // Find max lengths per column
          const maxPused = Math.max(...rows.map(r => r.pused.length));
          const maxTotal = Math.max(...rows.map(r => r.total.toString().length));
          const maxSuccess = Math.max(...rows.map(r => r.success.toString().length));
          const maxFailed = Math.max(...rows.map(r => r.failed.toString().length));

          // Build header row
          const lines = [
            `${"PUSED".padEnd(maxPused)} | ${"SUCCESS".padEnd(maxSuccess)} | ${"FAILED".padEnd(maxFailed)}`
          ];

          // Build data rows
          rows.forEach(r => {
            lines.push(
              `${r.pused.padEnd(maxPused)} |   ${r.successPct}%   | ${r.failedPct}%`
            );
          });

          return lines;
        },
      },
    },
    legend: { display: false },
   

  },
  scales: {
    x: { ticks: { autoSkip: false } },
    y: { beginAtZero: true },
    
  },
  
};


  return (
    <div style={{ width: "100%", overflowX: "auto" ,  }}>
      {chartData ? (
        <div style={{ minWidth: `${chartData.labels.length * 40}px`, height: "400px", }}>
          <Bar data={chartData} options={options} />
        </div>
      ) : (
        <p>Loading chart.Please wait</p>
      )}
    </div>
  );
};

export default SaBarChart;
